import React, { Component } from 'react';
import './PortfolioMedia.css';



class PortfolioMedia extends Component {
  render() {
    return (
      <div className="PortfolioMedia">
        <div className="PortfolioMedia-container">
          <img className="PortfolioMedia-background" src={this.props.portfolioOngoing.backgroundLarge} alt={this.props.portfolioOngoing.title}></img>
          <div className="PortfolioMedia-logoContainer">
            <img className="PortfolioMedia-logo" src={this.props.portfolioOngoing.icon} alt={this.props.portfolioOngoing.title}></img>
            <img className="PortfolioMedia-background-mobile" src={this.props.portfolioOngoing.background} alt={this.props.portfolioOngoing.title}></img>
          </div>
          <div className="PortfolioMedia-textContainer">
            <p style={{ color: this.props.colorTitle }} className="PortfolioMedia-title">
              {this.props.portfolioOngoing.title}
            </p>
            <p style={{ color: this.props.color }} className="PortfolioMedia-description"> {this.props.portfolioOngoing.description}</p>
          </div>
        </div>
      </div>
    );
  }
}

export default PortfolioMedia;